.detailwrapper{
        position: fixed;
        right: -100%;
        top: 0;
        bottom: 0;
        height: 100vh;
        overflow: hidden;
        overflow-y: auto;
        width: 70%;
        background-color: #fff;
        z-index: 2;
        transition: all .5s linear;
    }
    .open .detailwrapper{
        right: 0;
    }
    .mask{
        position: absolute;
        background-color: rgba(0,0,0,0.8);
        top: 0;
        left: 0;
        right: 0;
        bottom: 0;
        display: none;
        cursor: pointer;
    }
    .open .mask{
        display: block;
    }
    .close {
        color: #333;
        font-size: 26px;
        z-index: 1;
        position: absolute;
        right: 10px;
        top: 10px;
        padding: 4px 8px;
        background-color: #fff;
        display: inline-block;
        width: 30px;
        height: 30px;
        border-radius: 50%;
        cursor: pointer;
    }
  .headinfo{
    height: 300px;
    position: relative;
    padding: 40px 20px 20px;
    color: #fff;
}
  .headinfo figure{
      position: absolute;
      left: 0;
      right: 0;
      top:0;
      bottom: 0;
  }
  .headinfo figure:before{
    content: '';
    background-color: rgba(0,0,0,.5);
    position: absolute;
    top: 0;
    left: 0;
    right: 0;
    bottom: 0;
}
  .headinfo figure img{
    height: 100%;
    width: 100%;
    object-fit: cover;
}
  .headinfo .info{
    position: relative;
    z-index: 1;
    height: 100%;
  }
  .headinfo .info h1{
      font-size: 24px;
  }
  .headinfo .info > span {
    padding: 0 20px 0 0;
    display: inline-block;
  }

  .headinfo .info > div{
      margin: 10px 0;
  }
  .headinfo .info > div li{   
    display: inline-block;
    padding: 2px 20px;
    background-color: #f5f5f5;
    color: #333;
    margin-right: 20px;
    border-radius: 17px;
    font-size: 12px;
    font-weight: 600;
    margin: 5px 15px 5px 0;
}

.moviesinfo{
    padding: 20px;
}

.moviesinfo p{
    margin-bottom: 20px;
}
.screenshot iframe,
.screenshot img{
        display: inline-block;
        width: 25%;
        height: 200px;
        padding: 10px;
    }
    .cast li{
        display: inline-block;
        width: 33.3%;
        margin-bottom: 20px;
    }

    .cast li figure {
            display: inline-block;
            width: 60px;
            height: 60px;
            border-radius: 50%;
            overflow: hidden;
            vertical-align: middle;
        }
        .cast li > div{
            display: inline-block;
            vertical-align: middle;
            padding: 0 15px;
        }
        
    .socialinterest{
        position: absolute;
        bottom: 0;
        right: 0;
    }
    .socialinterest span{
        display: inline-block;
        padding: 0 10px;
    }
    .suggestionlist,
    .downloadlink,
    .screenshot{
        white-space: nowrap;
        overflow: hidden;
        overflow-x: auto;
        padding: 10px;
        margin-bottom: 15px;
    }
    .screenshot img,
    .screenshot iframe,
    .suggestionlist li,
    .downloadlink li{
        white-space: normal;
        display: inline-block;
        vertical-align: top;
        width: 24%;
        min-width: 200px;
        box-shadow: 0 0 7px 1px #ddd;
        padding: 20px;
        border-radius: 3px;
        margin-right: 10px;
    }

    .screenshot img,
    .screenshot iframe{
        padding: 0;
        width: 320px;
    }
    
    .downloadlink li span{
        display: block;
    }
    .downloadlink li .title{
        font-size: 28px;
        margin-bottom: 10px;
    }
    .downloadlink a{
        font-weight: 600;
        text-decoration: underline;
    }
@media(max-width:780px){
    .detailwrapper{
        width: 100%;
    }
    .cast li{        
        width: 49%;
        padding: 0 10px;
    }
    .cast li > div{
        padding: 5px 0 0;
    }
}
    @media(max-width:380px){
        .screenshot img,
        .screenshot iframe{
            width:280px
        }
    }